@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700&display=swap');

body {
    background: rgb(252, 252, 252);
    box-sizing: border-box;
}

.logo {
    font-family: 'Montserrat', Arial, Helvetica, sans-serif;
    font-weight: lighter;
    text-align: center;
    margin: 1em;
}

h1,h2 {
    font-family: 'Montserrat', Arial, Helvetica, sans-serif;
    font-weight: lighter;
}

label, input, button, a {
    font-family: 'Montserrat', Arial, Helvetica, sans-serif;
    font-weight: regular;
}

button {
    margin-right: 10px;
}

.canvas-element {
    position: relative !important;
    min-width: 600px;
    max-width: 600px;
    min-height: 600px;
    max-height: 600px;
    z-index: 8;
    background: #fff;
    background-size: cover;
    background-position: center center;
    overflow-wrap: break-word;
    overflow: hidden;
    object-fit: cover;
    padding: 0;
}

.pixabay {
    background: white;
    position: absolute;
    top: 4%;
    right: 1%;
    padding: 10px;
}

.canvas-element img {
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.overlay {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
}

.image-selector {
    position: fixed;
    overflow: scroll;
    width: 100vw;
    height: 100vh;
    background: #111;
    top: 0;
    z-index: 9;
    left: 0;
    color: white;
    padding: 40px 0px;
    transition: 0.2s ease-out all;
}

.closed {
    opacity: 0;
    transform: translateY(100%);
}

.opened {
    opacity: 1;
    transform: translateY(0);
}

.thumbnail {
    width: 33%;
    cursor: pointer;
    padding: 10px;
}

.small-text {
    font-size: 15px;
    position: absolute;
    bottom: 15px;
}

.footer {
    font-family: 'Montserrat', Arial, Helvetica, sans-serif;
    margin: 2em 0;
}